import Json from '../../../Json.js'
export default {
  data() {
    return {
      orderType: "",
      orderTypeIndex: 0,
      orderTypeList: Json.orderTypeList,
      chanelIndex: 0,
      chanelList: Json.chanelList,
      monthIndex: [0, 0],
      monthList: Json.monthList,
      transactionList: []
    }
  },
  methods: {
    orderTypeChange: function(e) {
      this.orderTypeIndex = e.detail.value
      this.queryTradingRecords();
    },
    chanelChange: function(e) {
      this.chanelIndex = e.detail.value
      this.queryTradingRecords();
    },

    bindMonthChange: function(e) {
      this.monthIndex = e.detail.value;
      this.queryTradingRecords();
    },
    //查询加油明细
    queryTradingRecords() {
      this.$minApi.uniapp_post(this.$base_api.queryTradingRecords, {queryFields:{
        month: this.monthList[0][this.monthIndex[0]]+"-"+this.monthList[1][this.monthIndex[1]],
        channelId: this.chanelList[this.chanelIndex].value,
      }}, "json").then(res => {
        if (res.success) {
          console.log("查询结果", res.data);
          if(res.data != null){
            this.transactionList = res.data.content;
          }else{
            this.transactionList = [];
          }
        } else {
          this.$msg('查询加油明细信息失败{}', res.msg);
        }
      })
    }
  }, 
  onLoad() {
    var date = new Date();
    this.monthIndex = [0,date.getMonth()];
    this.queryTradingRecords();
  },
  onPullDownRefresh(){
  	this.queryTradingRecords();
    wx.stopPullDownRefresh();
  }
}
